<template>
  <form @submit.prevent="submitForm">
    <div class="form-control" :class="{ invalid: !firstName.isvalid }">
      <label for="firstname">FirstName</label>
      <input type="text" id="firstname" v-model.trim="firstName.val" />
      <p v-if="!firstName.isvalid">first name should not be empty</p>
    </div>
    <div class="form-control" :class="{ invalid: !lastName.isvalid }">
      <label for="lastname">LastName</label>
      <input type="text" id="lastname" v-model.trim="lastName.val" />
      <p v-if="!lastName.isvalid">last name should not be empty</p>
    </div>
    <div class="form-control" :class="{ invalid: !description.isvalid }">
      <label for="description">Description</label>
      <textarea
        id="description"
        rows="5"
        v-model.trim="description.val"
      ></textarea>
      <p v-if="!description.isvalid">description should not be empty</p>
    </div>
    <div class="form-control" :class="{ invalid: !rate.isvalid }">
      <label for="rate">Hourly Rate</label>
      <input type="number" id="rate" v-model.number="rate.val" />
      <p v-if="!rate.isvalid">rate should be greater than 0</p>
    </div>

    <div class="form-control" :class="{ invalid: !areas.isvalid }">
      <h3>Areas of Expertise</h3>
      <div>
        <input
          type="checkbox"
          id="frontend"
          value="frontend"
          v-model="areas.val"
        />
        <label for="frontend">Frontend Development</label>
      </div>
      <div>
        <input
          type="checkbox"
          id="backend"
          value="backend"
          v-model="areas.val"
        />
        <label for="backend">Backend Development</label>
      </div>
      <div>
        <input type="checkbox" id="career" value="career" v-model="areas.val" />
        <label for="career">Career Advisory</label>
      </div>
      <p v-if="!areas.isvalid">At least one expertise must be selected</p>
    </div>
    <p v-if="!formIsValid">Please fix above errors</p>
    <base-button>Register</base-button>
  </form>
</template>

<script>
export default {
  emits: ["save-data"],
  data() {
    return {
      firstName: {
        val: "",
        isvalid: true,
      },
      lastName: {
        val: "",
        isvalid: true,
      },
      description: {
        val: "",
        isvalid: true,
      },
      rate: {
        val: null,
        isvalid: true,
      },
      areas: {
        val: [],
        isvalid: true,
      },
      formIsValid: true,
    };
  },
  methods: {
    validateForm() {
      this.formIsValid = true;
      if (this.firstName.val === "") {
        this.firstName.isvalid = false;
        this.formIsValid = false;
      }
      if (this.lastName.val === "") {
        this.lastName.isvalid = false;
        this.formIsValid = false;
      }
      if (this.description.val === "") {
        this.description.isvalid = false;
        this.formIsValid = false;
      }
      if (!this.rate.val || this.rate.val < 0) {
        this.rate.isvalid = false;
        this.formIsValid = false;
      }
      if (this.areas.val.length === 0) {
        this.areas.isvalid = false;
        this.formIsValid = false;
      }
    },
    submitForm() {
      this.validateForm();

      if (!this.formIsValid) {
        return;
      }
      const formData = {
        first: this.firstName.val,
        last: this.lastName.val,
        desc: this.description.val,
        rate: this.rate.val,
        areas: this.areas.val,
      };
      this.$emit("save-data", formData);
    },
  },
};
</script>


<style scoped>
.form-control {
  margin: 0.5rem 0;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

input[type="checkbox"] + label {
  font-weight: normal;
  display: inline;
  margin: 0 0 0 0.5rem;
}

input,
textarea {
  display: block;
  width: 100%;
  border: 1px solid #ccc;
  font: inherit;
}

input:focus,
textarea:focus {
  background-color: #f0e6fd;
  outline: none;
  border-color: #3d008d;
}

input[type="checkbox"] {
  display: inline;
  width: auto;
  border: none;
}

input[type="checkbox"]:focus {
  outline: #3d008d solid 1px;
}

h3 {
  margin: 0.5rem 0;
  font-size: 1rem;
}

.invalid label {
  color: red;
}

.invalid input,
.invalid textarea {
  border: 1px solid red;
}
</style>